$page-black: rgb(51, 51, 51);
$page-white: rgb(248, 248, 248);

.bm-burger-button {
  display: none;
}

.App {
  height: auto;
  overflow: hidden;

  .navigation-margin {
    height: 65px;
  }

  .Content {
    width: 100vw;
    display: grid;
    place-items: center;
    padding-bottom: 100px;
  }
}

@media screen and (max-width: 900px) {
  .bm-burger-button {
    display: block;
    position: fixed;
    width: 36px;
    height: 25px;
    right: 5vw;
    top: 17.5px;
}
.bm-burger-bars {
  background: black;
  transition-duration: 0.25s;
}
.bm-burger-bars-hover {
  background: pink;
}
.bm-cross {
    background: white;
}
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: black;
  padding: 2.5em 1.5em 0;
  font-size: 1.5em;
}
.bm-item-list {
  padding: 1em;
} 
.hamburger-menu-link {
  margin-bottom: 25px;
  font-size: 1.25rem;
  font-family: "Sunflower", "sans-serif";
  letter-spacing: 1px;
  color: white;
  text-decoration: none;
}
}